<script>
	import draggable from 'vuedraggable'
	import { makeSpTemplate, getOriTem, getTemplateGroup } from '../../config/api'
	import Bus from '../../config/bus'
	import { dialogConfirmHasbtn, dialogConfirm } from '../../config/mUtils'
	import applyData from '../../config/applyset'
	//import showTemplistComponent from './showTemplistComponent.vue' //
	export default {
		data() {
			return {
				templateId: 0,
				//
				tablistCSelected: 0,
				tablistC: [{
					id: 0,
					name: ''
				}, {
					id: 1,
					name: '',
					info: ''
				}],
				//
				controlList: [],
				//
				groupControlList: [],
				//
				currentShowId: 0,
				controlShowList: [],
				hasGroupcontrl: false, //
				groupControl: '', //control
				//
				tablistSSelected: 0,
				tablistS: [{
					id: 0,
					name: ''
				}, {
					id: 1,
					name: ''
				}],
				//
				controlSetList: [],
				//
				controlSet: [],
				//
				formSet: {
					name: '',
					icon: '',
					type: '',
					number: '',
					desc: '',
				},
				//
				applyTypeDisabled: false,
				//
				applyTypelist: [],
				//
				applyicon: [],
				//
				controlgroupSet: ''
			}
		},
		components: {
			draggable,
			showTemplistComponent: () =>
				import('./showTemplistComponent.vue')
		},
		created() {
			this.controlList = applyData.controlList;
			this.groupControlList = applyData.groupControlList;
			this.controlSetList = applyData.controlSetList;
			this.controlgroupSet = applyData.controlgroupSet;
			// console.log('this.controlList================')
			// console.log(this.controlList)
			// console.log('this.groupControlList================')
			// console.log(this.groupControlList)
			// console.log('this.controlSetList================')
			// console.log(this.controlSetList)
			// console.log('this.controlgroupSet================')
			// console.log(this.controlgroupSet)
			this.initicons();
			this.getTemplateGroup();
			if(this.$route.query.id != undefined) {
				this.templateId = this.$route.query.id;
				this.getData();
			}
		},
		mounted() {
			// Bus.$on('setChildShowlist', (pindex, event) => {
			//   if (event.added != undefined) {
			//     let index = event.added.newIndex;
			//     let element = JSON.parse(JSON.stringify(event.added.element));
			//     let setlist = this.controlSetList.filter(item => {
			//       return item.control == element.control
			//     })
			//     setlist = JSON.parse(JSON.stringify(setlist));
			//     element.setlist = setlist[0].list;
			//     this.controlShowList[pindex].controlShowList.push(element);
			//     //this.controlSet = setlist[0].list;
			//   }
			// })
		},
		methods: {
			chooseGoOutType(index,value){
				if (value == 'day') {
					var format = 'yyyy-MM-dd';
				} else{
					var format = 'yyyy-MM-dd HH:mm';
				}
				this.controlShowList[this.currentShowId].children[0].options[index].format = format;
			},
			///
			initData() {
				this.controlShowList = [];
				this.controlSet = [];
				this.formSet = {
					name: '',
					icon: '',
					type: '',
					number: '',
					desc: '',
				};
				this.tablistSSelected = -1;
				this.tablistCSelected = 0;
				this.formSet.icon = this.applyicon[0].iconname;
			},
			getData() {
				getOriTem({
					tid: this.templateId
				}).then(res => {
					if(res.data.msgNo == 1) {
						let payload = res.data.payload;
						//
						this.groupControl = payload.control;
						if(payload.control != 0) {
							this.hasGroupcontrl = true;
						}
						//
						for(let i in this.formSet) {
							if(payload[i] != undefined) {
								this.formSet[i] = payload[i]
							}
						}

						this.controlShowList = payload.data;
						this.controlShowList.forEach(child => {
							let setlist = this.controlSetList.filter(item => {
								return item.control == child.control
							})
							setlist = JSON.parse(JSON.stringify(setlist));
							if(child.control < 0) {
								child.setlist = setlist[0];
							} else {
								child.setlist = setlist[0].list;
							}
							if(child.control == 16 || child.control == 24) {
								child.value = child.value[0].name;
							}
							if(child.control == 17) {
								let val = [];
								child.value.forEach(iv => {
									val.push(iv.name)
								});
								child.value = val;
							}
						})
						console.log(11111111);
						console.log(this.controlShowList);
					} else {
						dialogConfirm('info', res.data.message)
					}
				})
			},
			getTemplateGroup() {
				getTemplateGroup({}).then(res => {
					if(res.data.msgNo == 1) {
						this.applyTypelist = res.data.payload;
					}
				})
			},
			//
			submitTemp(status) {
				// return false;
				if(this.controlShowList.length == 0) {
					dialogConfirm('info', '');
					return;
				}
				if(this.formSet.name == '') {
					dialogConfirm('info', '');
					return;
				}
				if(this.formSet.type == '') {
					dialogConfirm('info', '');
					return;
				}
				////////
				if(this.formSet.number != '') {
					let testnumber = /^[A-Z]{1}[A-Z0-9()_-]{1,9}$/gi.test(this.formSet.number);
					if(!testnumber) {
						dialogConfirm('info', '');
						return;
					}
				}
				//
				let commitdata = JSON.parse(JSON.stringify(this.controlShowList))
				this.setCommitdata(commitdata);

				this.controlShowList.forEach((item, index) => {
					item.fields = item.componentName + '_' + (index + 1);
					if(item.control == 16 || item.control == 24) {
						let obj = {
							name: item.value
						};
						item.value = [];
						item.value.push(obj);
					}
					if(item.control == 17) {
						let val = [];
						item.value.forEach(iv => {
							let obj = {
								name: iv
							};
							val.push(obj)
						});
						item.value = val;
					}
				})
				let param = {
					name: '',
					icon: '',
					type: '',
					number: '',
					data: '',
					desc: ''
				}
				for(let i in param) {
					if(this.formSet[i] != undefined) {
						param[i] = this.formSet[i]
					}
				}
				if(this.templateId != 0) {
					param.tid = this.templateId;
				}

				param.data = JSON.stringify(commitdata);
				param.status = status;

				// console.log(param);
				makeSpTemplate(param).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', res.data.message);
						//this.initData();
						this.$router.push({
							name: 'applytempListNew'
						})
					} else {
						dialogConfirm('fail', res.data.message);
					}
				})
			},
			setCommitdata(data) {
				data.forEach(item => {
					// if (item.value == undefined && item.children == undefined) {
					//   item.value = ''
					// }
					if(item.setlist != undefined) {
						delete item.setlist;
					}
					if(item.options != undefined) {
						let optionlist = [];
						let optionArray = [];
						item.options.forEach(option => {
							if(optionlist.indexOf(option.name) == -1) {
								optionlist.push(option.name);
								optionArray.push(option);
							}
						})
						//optionlist = Array.from(new Set(optionlist));
						item.options = optionArray;
						// optionlist.forEach(ol => {
						//   item.options.push({
						//     name: ol
						//   })
						// })
					}
					if(item.control != undefined && item.control == 15) {
						let hash = {};
						item.value = item.value.reduceRight((item, next) => {
							hash[next.name] ? '' : hash[next.name] = true && item.push(next);
							return item
						}, []);
					}
					if(item.children != undefined) {
						this.setCommitdata(item.children);
					}
				})
			},
			//
			uniq(array) {
				var set1 = Array.from(new Set(array))
				return set1;
			},
			////
			initicons() {
				for(let i = 1; i < 70; i++) {
					let iconname = 'sp' + i + '_1.png';
					let path = '../../static/images/' + iconname;
					this.applyicon.push({
						iconname: iconname,
						path: path
					})
				}
				this.formSet.icon = this.applyicon[0].iconname
			},
			//icon
			changeIconselect(index) {
				this.formSet.icon = this.applyicon[index].iconname;
			},
			//
			deleteOption(list, index) {
				if(list.length == 1) {
					dialogConfirm('info', '')
				} else {
					list.splice(index, 1)
				}
			},
			gotoApplylist() {
				dialogConfirmHasbtn('').then(res => {
					this.$router.push({
						name: 'applytempListNew'
					})
				}).catch(() => {})
			},
			//
			addOption(list, index) {
				// console.log(list);
				// console.log(index);
				// return false;
				let testlist = JSON.parse(JSON.stringify(list));
				if(list[0].scal_unit != undefined) {
					list.splice(index + 1, 0, {
						name: "",
						scal_unit: 'day'
					})
				} else if(list[0].value != undefined) {
					list.splice(index + 1, 0, {
						name: "",
						value: 0
					})
				}
			},
			//item
			deleteShowitem(index) {
				if(this.controlShowList[index].control < 0) {
					this.hasGroupcontrl = false;
					this.applyTypeDisabled = false;
					this.groupControl = '';
				}
				this.controlShowList.splice(index, 1);
				this.controlSet = [];
			},
			//item
			showlistAdded(event) {
				if(event.added != undefined) {
					let index = event.added.newIndex;
					this.currentShowId = index;
					let csl = JSON.parse(JSON.stringify(this.controlShowList));
					let setlist = this.controlSetList.filter(item => {
						return item.control == csl[index].control
					})
					setlist = JSON.parse(JSON.stringify(setlist));
					if(csl[index].control > 0) {
						setlist[0].list.forEach(item => {
							if(item.field == 'skip') {
								csl[index].skip[0].skip_site = item.value;
							} else {
								csl[index][item.field] = item.value
							}

						})
						csl[index].setlist = setlist[0].list;
						this.controlSet = setlist[0].list;
					} else {
						this.hasGroupcontrl = true;
						this.groupControl = csl[index].control;
						csl[index].setlist = setlist[0];
						this.controlSet = setlist[0];
						this.applyTypeDisabled = true;
					}
					this.controlShowList = csl;
				}
			},
			//item
			changecurrentShowId(index) {
				this.currentShowId = index;
				this.tablistSSelected = 0
				if(this.controlShowList[index].control > 0) {
					this.controlSet = this.controlShowList[index].setlist;
				} else {
					this.controlSet = {
						control: this.controlShowList[index].control
					}
				}
			},
			///tab
			changeTabselect(idname, id) {
				this[idname] = id;
			},
			removeHandle(event) {},
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/modal.scss';
	@import '../../style/createapply.scss';
</style>


